/**
 * @Date: 2021-01-12 20:30:31
 * @author: zjs
 * @description: 验证码是否过期
 */
export function isCodeOverdue () {
  const getCodeTime = localStorage.getItem('getCodeTime')
  return (new Date().getTime() - getCodeTime) < 60 * 1000 * 15
}
/**
 * @Date: 2021-01-12 20:30:31
 * @author: zjs
 * @description: 提交表单返回赋值
 */
export function initPhoneForm () {
  const { phone, code } = JSON.parse(localStorage.getItem('lastPayQuery')) || {}
  this.phone = phone
  if (!isCodeOverdue()) return
  this.code = code
}
