import {
  clickLog
} from '@/helper/clickLog'
import { postBack } from '@/helper/payConfig'
import { getSearch } from '@/utils'
import { throwConfig } from '@/helper/abKeyConfig'
/**
 * @Date: 2021-01-04 15:06:07
 * @author: zjs
 * @description: 百度H5AB支付成功回调
 */
export function paySuccessBack () {
  clickLog({
    module_ori: '表单提交',
    module_part: this.SUBMIT_BTN
  })
}
/**
 * @Date: 2021-01-04 15:06:07
 * @author: zjs
 * @description: 百度H5AB支付成功回调
 */
export function payErrorBack (data) {
  console.log(data)
  const codeMap = {
    33002: this.ERROR_CODE,
    30001: this.STUDENT_OVER
  }
  if (!codeMap[data.code]) return
  clickLog({
    module_ori: '表单错误模块',
    module_part: codeMap[data.code]
  })
}
/**
 * @Date: 2021-01-04 15:22:25
 * @author: zjs
 * @description: 获取验证码成功回调
 */
export function getCodeSuccessBack () {
  clickLog({
    module_ori: '表单提交',
    module_part: this.GET_CODE
  })
}
/**
 * @Date: 2021-01-04 15:22:25
 * @author: zjs
 * @description: 获取验证码成功回调
 */
export function getCodeErrorBack () {
  clickLog({
    module_ori: '表单错误模块',
    module_part: this.ERROR_PHONE
  })
}

/**
 * @Date: 2021-01-15 22:28:02
 * @author: zjs
 * @description: 整理支付参数
 */
export function getPayQuery (otherQuery) {
  const {
    phone,
    code,
    paytype,
    successPag
  } = this
  const id = getSearch('id')
  const {
    origin,
    pathname
  } = location
  const groupInfo = JSON.parse(localStorage.getItem('groupInfo'))
  const queryModel = {
    return_url: encodeURIComponent(`${origin}${pathname}#/${successPag}`),
    phone,
    code,
    product_sn: 'PRODUCT_SN_XIAOBAI_12',
    pay_type: paytype,
    id,
    ...postBack(),
    // 大数据要的参数
    ext_param: [{
      ...groupInfo,
      ...throwConfig
    }],
    ...otherQuery
  }
  return queryModel
}
