import Store from '@/store'
import Router from '@/router'
import { postBack } from '@/helper/payConfig'
import { isPhone } from '@/utils'

// 根据反馈的code判断跳转链接
const hrefConfigList = {
  PRODUCT_SN_XIAOBAI_12: 'SuccessEnrollWM',
  PRODUCT_SN_XIAOBAI_7: 'SuccessEnrollWM',
  PRODUCT_SN_XIAOBAI_1: 'SuccessEnrollMH',
  PRODUCT_SN_XIAOBAI_0: 'SuccessEnrollMH'
}
// 需要跳转的code
const hrefErrorCode = [
  30006, // 已经购买过同一种
  30009 // 已经购买过同一类,
]
/**
 * @Date: 2021-01-15 16:30:35
 * @author: zjs
 * @description: 根据code 判断是否需要跳转
 */
export function isHrefByCode (result) {
  const { code, data } = result
  if (hrefErrorCode.includes(code) && hrefConfigList[data.product_sn]) {
    localStorage.setItem('guide_order_num', data.order_num)
    Router.replace({ name: 'Officals' })
    return code
  }
  return code
}

export async function isBuyOrder (val) {
  // 获取上一次订单的手机号
  const buyPhone = localStorage.getItem('buyPhone')
  const phone = val || buyPhone
  if (!isPhone(phone)) return
  await Store.$apis.payment.checkOrder({ phone }).catch(err => {
    isHrefByCode(err)
    return Promise.reject(err)
  })
}
/**
 * @Date: 2021-01-14 17:50:43
 * @author: zjs
 * @description: mid查询是否购买
 */
export async function isBuyOrderMid () {
  await Store.$apis.payment.checkOrderInWx().catch(err => {
    isHrefByCode(err)
    return Promise.reject(err)
  })
}

/**
  * @Date: 2020-12-29 12:34:40
  * @author: zjs
  * @description:提交支付
  */
export async function submitPay (model) {
  const { apiQuery, successBack, payOverBack, errorBack } = model || {}
  try {
    typeof successBack === 'function' && successBack()
    const { phone, code, paytype } = apiQuery
    // 存储手机号验证码等
    localStorage.setItem('buyPhone', phone)
    localStorage.setItem('guide_phone', phone)
    localStorage.setItem('code', code)
    localStorage.setItem('payType', paytype)
    localStorage.setItem('lastPayQuery', JSON.stringify(apiQuery))
    const data = await this.$apis.payment.pay(apiQuery)
    const url = data.url
    if (url) {
      // 记录打开挽留弹窗
      localStorage.setItem('backDialogMark', true)
      this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', true)
      // 支付前路由存储
      typeof payOverBack === 'function' && payOverBack()
      location.href = decodeURIComponent(url)
    } else {
      typeof payOverBack === 'function' && payOverBack()
      // 记录打开挽留弹窗
      localStorage.setItem('backDialogMark', true)
      this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', true)
      // 更新阿里支付表单
      const isReSet = document.getElementById('aLiPay')
      if (isReSet) { isReSet.parentNode.removeChild(isReSet) }
      const div = document.createElement('div')
      div.id = 'aLiPay'
      div.innerHTML = data
      document.body.appendChild(div)
      document.forms[0].submit()
    }
  } catch (error) {
    const { data } = error
    typeof errorBack === 'function' && errorBack(data)
  }
}
/**
 * @Date: 2021-01-18 10:19:32
 * @author: zjs
 * @description: 提交校验
 */
export async function wxSubmitValed (model) {
  try {
    const { phone, code } = model
    // 手机号校验
    // eslint-disable-next-line prefer-promise-reject-errors
    if (!isPhone(phone)) return Promise.reject({ code: 33001, msg: '手机号有误，请重填' })
    // 验证码校验
    await Store.$apis.userOperator.phoneCheckCode({ phone, code })
    // 订单校验
    await Store.$apis.payment.checkOrderInWx()
  } catch (error) {
    return Promise.reject(error.data)
  }
}

/**
 * @Date: 2021-01-19 16:29:55
 * @author: zjs
 * @description: 微信内支付
 */
export async function inWxPay (model) {
  try {
    const result = await Store.$apis.payment.payInWx({
      pay_type: 2,
      pay_mode: 3,
      ...model,
      ...postBack()
    })
    return new Promise((resolve) => {
      window.WeixinJSBridge.invoke('getBrandWCPayRequest', result,
        (res) => {
          resolve(res.err_msg)
        })
    })
  } catch (error) {
    return Promise.reject(error.data)
  }
}
