<template>
  <div class="page-baiduABPay">
    <div class="content">
      <!-- 顶部图片 -->
      <!-- <div class="discount-bg"> -->
      <!-- 倒计时人数 -->
      <!-- <CountPeople
          class="discount-quotas discount-item"
          #default="countPeopleSlot">
          <template>
            <div class="discount-quotas__title">剩余优惠名额</div>
            <div class="discount-quotas__number">
              仅剩{{ countPeopleSlot.peopleNumber }}名
            </div>
          </template>
        </CountPeople> -->
      <!-- 倒数计时间 -->
      <!-- <CountDown class="discount-item mt__30" #default="countDownSlot">
          <template>
            <div class="pay-title">剩余支付时间</div>
            <VanCountDown
              class="pay-time-box"
              :time="countDownSlot.overMinute" />
          </template>
        </CountDown> -->
      <!-- </div> -->
      <!-- 手机号部分 -->
      <div class="card-box phone-box mt__20">
        <VanField
          v-model.trim="phone"
          clearable
          label="手机号"
          placeholder="请输入手机号">
        </VanField>
        <VanField
          v-model.trim="code"
          clearable
          label="验证码"
          placeholder="请输入验证码">
          <template #button>
            <CountDownBtn
              class="phone-code"
              :phone="phone"
              :get-code-success-back="getCodeSuccessBack"
              :get-code-error-back="getCodeErrorBack"></CountDownBtn>
          </template>
        </VanField>
      </div>
      <!-- 商品区域 -->
      <div class="card-box mt__20">
        <div class="shop-box">
          <div class="img-box">
            <img :src="require('@/assets/baiduABPay/moneyUp.png')" />
          </div>
          <div class="card-text-box">
            <div class="money_up-title">
              微淼小白理财训练营 1天1元学理财
            </div>
            <div class="text__box">
              <span class="text__money">￥12</span>
              <!-- <span class="text__original">市场价￥199</span> -->
            </div>
          </div>
        </div>
        <!-- 选择支付方式 -->
        <div class="pay-box mt__20">
          <div class="pay-text">支付方式</div>
          <VanRadioGroup v-model="paytype">
            <div
              :class="['pay-item', { mt__40: index }]"
              v-for="(item, index) in payTypeList"
              :key="index">
              <img :src="item.img" />
              <VanRadio :name="item.key" checked-color="#FB404A">{{
                item.label
              }}</VanRadio>
            </div>
          </VanRadioGroup>
        </div>
      </div>
    </div>
    <!-- 支付按钮  -->
    <div class="submit-box">
      <div class="text__box">
        <div class="text__money-icon">￥</div>
        <div class="text__money">12</div>
        <!-- <div class="text__original">市场价￥199</div> -->
      </div>
      <div
        @click="bottomPayClick"
        class="submit-btn">立即支付</div>
    </div>
    <BackDialog
      @backDiaLogSubmit="backDiaLogSubmit"></BackDialog>
  </div>
</template>
<script>
import { payLogConfig } from './config'
// import CountDown from '@/components/CountDown'
// import CountPeople from '@/components/CountPeople'
import Store from '@/store'
import BackDialog from '@/components/BaiduABBusniess/backDialog'
import { initPhoneForm } from '@/helper/backDiaLog'
import CountDownBtn from '@/components/CountDownBtn'
import { submitPay, isBuyOrder } from '@/helper/payMent'
import { getPayQuery, paySuccessBack, payErrorBack, getCodeSuccessBack, getCodeErrorBack } from '@/helper/baiduABPayMentBack'
import { clickLog } from '@/helper/clickLog'
import { isPhone } from '@/utils'
import { Dialog } from 'vant'

export default {
  // CountDown, CountPeople,
  components: { BackDialog, CountDownBtn },
  async beforeRouteEnter (to, form, next) {
    const groupInfo = JSON.parse(localStorage.getItem('groupInfo') || '{}')
    Store.$log.update({
      alternate: localStorage.getItem('throwOtherLog')
    })
    if (groupInfo.group !== 'C') {
      return next({ name: 'BaiduAB', query: to.query })
    }
    next()
  },
  data () {
    return {
      phone: '', // 手机号
      code: '', // 验证码
      timer: null, // 定时器
      paytype: 2, // 支付类型
      successPag: 'Officals', // 支付后要跳转的成功页面
      ...payLogConfig, // 埋点数据
      payTypeList: [
        {
          // 支付类型弹窗
          img: require('@/assets/baiduABPay/wxLogo.png'),
          label: '微信支付',
          key: 2
        },
        {
          img: require('@/assets/baiduABPay/aLiLogo.png'),
          label: '支付宝支付',
          key: 1
        }
      ]
    }
  },
  created () {
    this.isBuyOrder()
    this.timer = setInterval(async () => {
      const resultCode = await this.isBuyOrder()
      if (resultCode && resultCode !== 30000) {
        clearInterval(this.timer)
        this.timer = null
      }
    }, 3000)
    window.onpageshow = function (event) {
      if (event.persisted) {
        window.location.reload()
      }
    }
    this.initPhoneForm()
    const lastPayQuery = JSON.parse(localStorage.getItem('lastPayQuery')) || {}
    if (lastPayQuery && lastPayQuery.pay_type) {
      this.paytype = Number(lastPayQuery.pay_type) || 2
    }
  },
  beforeDestroy () {
    this.timer && clearInterval(this.timer)
  },
  methods: {
    // 判断是否有缓存有效手机号
    isBuyOrder,
    initPhoneForm,
    submitPay,
    paySuccessBack,
    payErrorBack,
    getCodeSuccessBack,
    getCodeErrorBack,
    getPayQuery,
    /**
     * @Date: 2021-01-19 16:02:31
     * @author: zjs
     * @description: 校验支付
     */
    async submitValed () {
      try {
        const { phone, code } = this
        if (!isPhone(phone)) {
          Dialog({ message: '手机号码有误，请重填' })
          clickLog({
            module_ori: '表单错误模块',
            module_part: this.ERROR_PHONE
          })
          return Promise.reject(phone || '手机号码有误，请重填')
        }
        await this.$apis.userOperator.phoneCheckCode({
          phone,
          code
        })
        await isBuyOrder(phone)
      } catch (error) {
        if (error.data.code === 33002) {
          clickLog({
            module_ori: '表单错误模块',
            module_part: this.ERROR_CODE
          })
        }
        return Promise.reject(error.data.code)
      }
    },
    /**
     * @Date: 2021-01-16 13:38:10
     * @author: zjs
     * @description: 底部按钮被点击
     */
    async bottomPayClick () {
      clickLog({
        module_ori: '底部悬浮栏',
        module_part: this.BOTTOM_BTN
      })
      await this.submitValed()
      this.submitPay({
        apiQuery: this.getPayQuery({
          return_url: encodeURIComponent(location.href)
        }),
        errorBack: this.payErrorBack
      })
    },
    async backDiaLogSubmit () {
      await this.submitValed()
      const otherQuery = JSON.parse(localStorage.getItem('lastPayQuery'))
      this.submitPay({
        apiQuery: this.getPayQuery({
          ...otherQuery,
          return_url: encodeURIComponent(location.href)
        }),
        successBack: this.paySuccessBack,
        errorBack: this.payErrorBack
      })
    }
  }
}
</script>
<style lang="scss">
.page-baiduABPay {
  width: 100%;
  background: #f5f5f5;
  min-height: 100vh;

  /deep/.van-field__label {
    width: 140px;
    font-size: 36px;
    font-weight: 500;
    color: #222;
  }

  /deep/.van-field__value {
    font-size: 35px;
    font-weight: 400;
    color: #d5d5d5;
  }

  img {
    vertical-align: middle;
  }

  .content {
    padding: 20px;
    padding-bottom: 130px;
    width: 100%;

    .discount-bg {
      background: #fff;
      border-radius: 15px;
      width: 100%;
      height: 146px;
      display: flex;

      .discount-item {
        margin-top: 35px;
        width: 50%;
        text-align: center;
      }

      .discount-quotas {
        .discount-quotas__title {
          font-size: 28px;
          font-weight: 400;
          color: #333;
        }

        .discount-quotas__number {
          margin-top: 10px;
          font-size: 36px;
          font-weight: 600;
          color: #fb404a;
        }
      }

      .pay-title {
        font-size: 28px;
        font-weight: 400;
        color: #333;
      }

      .pay-time-box {
        margin-top: 8px;
        display: flex;
        justify-content: center;
        font-size: 36px;
        font-weight: 600;
        color: #fb404a;
      }
    }

    .card-box {
      background: #fff;
      border-radius: 15px;
      padding: 20px;
    }

    .phone-title {
      font-size: 35px;
      font-weight: 400;
      color: #000;
    }

    .phone-code {
      @include horVerCenter;

      width: 196px;
      height: 54px;
      border: 2px solid #fb404a;
      border-radius: 10px;
      font-size: 28px;
      font-weight: 400;
      color: #fb404a;
    }
    // 输入框基本样式
    .baseInput {
      font-size: 30px;
      font-weight: 400;
      color: #9a9a9a;
    }

    .shop-box {
      display: flex;
      padding-bottom: 40px;
      border-bottom: 1px solid #f2f2f2;

      .img-box {
        width: 183px;
        height: 190px;
        border-radius: 10px;
        overflow: hidden;
        margin-left: 10px;

        img {
          width: 100%;
          height: 100%;
        }
      }

      .card-text-box {
        margin-left: 20px;
        margin-top: 10px;
        width: calc(100% - 220px);

        .money_up-title {
          font-size: 32px;
          line-height: 44px;
          font-weight: 500;
          color: #333;
          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
        }

        .text__box {
          margin-top: 25px;
          display: flex;
          align-items: flex-start;

          .text__money-icon {
            font-size: 48px;
            font-weight: 500;
            color: #fb404a;
          }

          .text__money {
            font-size: 80px;
            font-weight: 600;
            color: #fb404a;
          }

          .text__original {
            font-size: 28px;
            color: #aaa;
            font-weight: 400;
            margin-left: 10px;
            line-height: 120px;
            text-decoration: line-through;
          }
        }
      }
    }

    .pay-box {
      padding: 0 10px 40px;

      .pay-text {
        font-size: 26px;
        font-weight: 400;
        color: #aaa;
        margin-top: 30px;
        margin-bottom: 40px;
      }

      .pay-item {
        display: flex;

        img {
          width: 40px;
          height: 37px;
        }

        .van-radio {
          font-size: 28px;
          font-weight: 400;
          color: #141414;
          margin-left: 10px;
          width: 100%;
          display: flex;
          flex-direction: row-reverse;
          align-items: center;
          justify-content: space-between;
        }
      }
    }
  }

  .phone-box {
    padding: 10px 20px !important;
  }

  .input__long {
    width: calc(100% - 8px);

    input {
      width: 100% !important;
    }
  }

  .submit-box {
    position: fixed;
    left: 0;
    bottom: 0;
    padding: 20px 50px;
    width: 100%;
    height: 130px;
    background: #fff;
    display: flex;
    justify-content: space-between;

    .text__box {
      display: flex;
      align-items: flex-end;

      .text__money-icon {
        line-height: 50px;
        font-size: 48px;
        font-weight: 500;
        color: #fb404a;
      }

      .text__money {
        line-height: 80px;
        font-size: 80px;
        font-weight: 600;
        color: #fb404a;
      }

      .text__original {
        font-size: 28px;
        color: #aaa;
        font-weight: 400;
        margin-left: 10px;
        line-height: 50px;
        text-decoration: line-through;
      }
    }

    .submit-btn {
      @include horVerCenter;

      width: 292px;
      height: 80px;
      background: #fb404a;
      border-radius: 40px;
      font-size: 32px;
      font-weight: 400;
      color: #fff;
    }
  }
}
</style>
