<template>
  <VanDialog
    v-model="backDialogMark"
    :show-confirm-button="false">
    <div class="components-backDialog">
      <div class="title">要放弃付款吗？</div>
      <!-- 优惠券内容 -->
      <div class="money-box">
        <!-- <div class="money">¥<span>187</span>元</div> -->
        <!-- <div class="money-topic">仅限今日使用</div> -->
      </div>
      <div class="btn-box">
        <div class="btn__notPay" @click="removeBackDialogMark">放弃支付</div>
        <div
          class="btn__pay"
          @click="payNext">继续支付 </div>
      </div>
    </div>
  </VanDialog>
</template>

<script>
import { clickLog } from '@/helper/clickLog'
import { isCodeOverdue } from '@/helper/backDiaLog'

export default {
  computed: {
    backDialogMark: {
      get () {
        return this.$store.state.ABTest.backDialogMark
      },
      set () {}
    }
  },
  created () {
    // 判断是否显示弹窗
    if (isCodeOverdue()) {
      setTimeout(() => {
        this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', !!JSON.parse(localStorage.getItem('backDialogMark')))
      }, 1300)
    }
  },
  methods: {
    /**
     * @Date: 2021-01-04 16:01:30
     * @author: zjs
     * @description: 移除挽留弹窗标记
     */
    removeBackDialogMark () {
      clickLog({
        module_ori: '支付挽留弹窗',
        module_part: '106_toufang_lcx_0115'
      })
      localStorage.removeItem('backDialogMark')
      this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', false)
    },
    /**
     * @Date: 2021-01-04 16:08:23
     * @author: zjs
     * @description: 继续支付
     */
    payNext () {
      clickLog({
        module_ori: '支付挽留弹窗',
        module_part: '106_toufang_lcx_0116'
      })
      this.$emit('backDiaLogSubmit')
      this.removeBackDialogMark()
    }
  }
}
</script>

<style lang="scss">
.components-backDialog {
  padding: 60px 40px 50px;
  background: #fff;
  border-radius: 20px;

  .title {
    font-size: 36px;
    font-weight: 500;
    color: #333;
    text-align: center;
  }

  .btn-box {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;

    .btn__notPay {
      @include horVerCenter;

      width: 240px;
      height: 80px;
      background: #e5e5e5;
      border-radius: 10px;
      font-size: 30px;
      font-weight: 500;
      color: #999;
    }

    .btn__pay {
      @include horVerCenter;

      width: 240px;
      height: 80px;
      background: linear-gradient(0deg, #ff093b, #ff7d8d);
      border-radius: 10px;
      font-size: 30px;
      font-weight: 500;
      color: #fff;
    }
  }

  .money-box {
    background: url('https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/img/d_bg.gif') no-repeat center;
    background-size: 100% 100%;
    width: 460px;
    height: 240px;
    margin: 50px auto 0;

    .money {
      text-align: center;
      padding-top: 40px;
      font-size: 45px;
      font-weight: 600;
      color: #ff1241;

      span {
        font-size: 90px;
      }
    }

    .money-topic {
      margin-top: 40px;
      font-size: 30px;
      font-weight: 400;
      color: #ff1241;
      text-align: center;
    }
  }
}
</style>
